* {
    margin: 0;
    padding: 0;
}

.maindiv {
    background-color: rgb(231, 233, 255);
    margin: 25px auto;
    border: 2px solid red;
    border-radius: 10px;
    width: 70%;
}

h2 {
    margin: 15px;
    text-align: center;
    color: blue;
}

hr {
    border: 2px solid;
}

.Txtdiv {
    display: flex;
    justify-content: center;
}

textarea {
    margin: 10px;
    padding: 10px;
    width: 50%;
    height: 100px;
    border: 3px solid rgb(64, 255, 0);
    border-radius: 5px;
}

.txtarea {
    color: red;
}

button {

    color: red;
    background: #d7f8dc;
    font-size: 16px;
    font-weight: bold;
    margin: 6px;
    padding: 3px;
    border: 2px solid blue;
    border-radius: 6px;
}

button:hover {

    color: #000;
    background: #fc0303;
    font-size: 16px;
    font-weight: bold;
    margin: 6px;
    padding: 3px;
    border: 2px solid blue;
    border-radius: 6px;
}

.allbtn {
    text-align: center;
    color: red;
}

.preview {
    border: 2px solid red;
    border-radius: 10px;
    background: #d1fdeb;
    text-align: center;
    width: 70%;
    margin: auto;
}

.pre {
    text-align: center;
}